:root{
  --primary: #fff;
}

.btn{
  padding: 8px 20px;
  border-radius: 2px;
  outline: none;
  border: none;
  cursor: pointer;
}

.btn--primary{
  background-color: var(--primary);
  color: #242424;
  /*border: 1px solid var(--primary);*/
}

.btn--outline{
  background:#242424;
  color: #fff;
  padding: 8px 20px;
  transition: all 0.3s ease-out;
}

.btn-medium{
  padding: 8px 20px;
  font-size: 18px;
}

.btn-large{
  background: #fff;
  color: #242424;
  padding: 12px 26px;
  font-size: 20px;
}

.btn--medium:hover, 
.btn--large:hover{
  background: #fff;
  color: #242424;
  transition: all 0.3s ease-out;
}